html {
  height: 100%;
}
body {
  height: 100%;
  display: flex !important;
  flex-direction: column;
  background: white;
}
body > header {
  text-align: center;
  background: #fb0f;
  color: white;
  display: none;
}
body > header.show-header {
  display: block !important;
}
body > .content {
  flex: 1;
  overflow: auto;
  display: flex;
}
body > .content .content-left {
  width: 250px;
  padding: 0 1px;
  overflow: auto;
  border-right: solid 1px #fb0f65;
}
body > .content .content-right {
  flex: 1;
  width: 200px;
  overflow: auto;
  padding: 10px;
  overflow-x: hidden;
}
body > .content .content-right > div {
  margin-bottom: 50px;
}
body > .content .content-right > div h4 {
  margin-bottom: 15px;
  color: #fb0f65;
}
body > .content .content-right [id] {
  padding: 5px 0 0 0;
}
body > .content code {
  font-size: 16px;
}
body > .content .description {
  padding: 5px 0 10px 20px;
}
.nav-not-parent .nav-parent-fold-icon {
  display: none !important;
}
.carousel-demo {
  background: black;
  text-align: center;
  position: relative;
}
.carousel-demo .carousel1 {
  height: 480px;
}
.carousel-demo .carousel1 li > div {
  height: 100%;
}
#signature {
  height: 500px;
  border: 2px dotted black;
  background-color: lightgrey;
}



